import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Carousel from "react-material-ui-carousel";
import { Accordion, Box, Button } from "@material-ui/core";
import android from "../../assets/android.png";
import react from "../../assets/react.png";
import flutter from "../../assets/flutter.png";
import qart2 from "../../assets/qart2.png";
import qart1 from "../../assets/qart1.png";
import dashimg from "../../assets/dashboard3.png";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    alignItems: "center",
  },
  mainheading: {
    height: 50,
    maxWidth: 320,
    display: "flex",
    justifyContent: "center",
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: 28,
    lineHeight: "113.6%",
    /* or 34px */

    color: "#3B3E56",
  },
  subheading: {
    display: "flex",
    maxWidth: 320,
    height: 40,
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: 20,
    lineHeight: "30px",
    color: "#000000",
  },
  text: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    maxWidth: 330,
    height: 142,
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 300,
    fontSize: 15,
    lineHeight: "20px",
    /* or  */
    color: "#000000",
  },
  lzsubheading: {
    display: "flex",
    maxWidth: 320,
    height: 40,
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: 19,
    lineHeight: "30px",
    color: "#000000",
  },
  lztext: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    maxWidth: 330,
    height: 142,
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 300,
    fontSize: 15,
    lineHeight: "20px",
    /* or  */
    color: "#000000",
  },
  circle: {
    width: 300.41,
    height: 300.41,
    background: "#F0F0F0",
    transform: "rotate(5.76deg)",
    borderRadius: "10rem",
  },
  smallcircle: {
    width: 90,
    height: 90,
    position: "absolute",
    borderRadius: "20rem",
    background: "#FFFFFF",
    boxShadow: "5px 4px 8px -2px rgba(0, 0, 0, 0.25)",
    marginLeft: "-10rem",
    marginTop: "12rem",
  },
  logo: {
    width: 80,
    height: 80,
    position: "absolute",
    borderRadius: "20rem",
    background: "#FFFFFF",
    boxShadow: "5px 4px 8px -2px rgba(0, 0, 0, 0.25)",
    marginLeft: "-10rem",
    marginTop: "12.3rem",
  },
  lzcircle: {
    width: 200.41,
    height: 300.41,
    background: "#F0F0F0",
    transform: "rotate(5.76deg)",
    borderRadius: "10rem",
  },
  loanzify: {
    position: "absolute",
    width: 270,
    height: 320,
    borderRadius: 28,
    marginLeft: "-3rem",
    marginTop: "11rem",
  },
  mobilefront: {
    position: "absolute",
    width: 256,
    height: 300,
    marginLeft: "-3rem",
    marginTop: "11rem",
  },

  androidimg: {
    width: 50.56,
    height: 60.82,
  },
  flutterimg: {
    width: 60.08,
    height: 60.82,
    left: 284,
  },
  androidtext: {
    width: 80.6,
    height: 13.19,

    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: 18,
    lineHeight: "24px",

    color: "#000000",
  },
  fluttertext: {
    width: 66.68,
    height: 13.19,
    fontFamily: "Axiforma",
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: 18,
    lineHeight: "30px",

    color: "#000000",
  },
  fields: {
    display: "flex",
    justifyContent: "space-between",
    width: 170,
    marginTop: 50,
    marginBottom: 50,
  },
}));

export default function ProjectsMobile(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Box className={classes.mainheading}>Our Recent Projects</Box>
      <Carousel effect="fade" dataInterval="false" autoPlay="false">
        <Box
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Box className={classes.subheading}>qartt</Box>
          <Box className={classes.text}>
            qartt provides an intuitive and seamless delivery service, allowing
            you to focus on your core product and services. Without the
            complication of coordinating an entire in-house delivery team, your
            resources are free to be utilized where they are needed the most.
          </Box>
          <Box className={classes.circle} />
          <img src={qart1} alt="" className={classes.mobilefront} />

          <Box className={classes.fields}>
            <Box>
              <img src={react} alt="" className={classes.flutterimg} />
              <Box className={classes.androidtext}>React Native</Box>
            </Box>
            <Box>
              <img src={flutter} alt="" className={classes.flutterimg} />
              <Box className={classes.fluttertext}>Flutter</Box>
            </Box>
          </Box>
        </Box>

        <Box
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Box className={classes.lzsubheading}>qartt partner</Box>
          <Box className={classes.lztext}>
            By joining qartt, you can pick up a great side-hustle with flexible
            hours and great pay. You will see your earnings in real-time, and
            see precisely how they are calculated. Your money is deposited
            weekly with zero delays.
          </Box>
          <Box className={classes.circle} />
          <img src={qart2} alt="" className={classes.mobilefront} />

          <Box className={classes.fields}>
            <Box>
              <img src={react} alt="" className={classes.flutterimg} />
              <Box className={classes.androidtext}>React Native</Box>
            </Box>
            <Box>
              <img src={flutter} alt="" className={classes.flutterimg} />
              <Box className={classes.fluttertext}>Flutter</Box>
            </Box>
          </Box>
        </Box>

        <Box
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Box className={classes.subheading}>dashboard.earth</Box>
          <Box className={classes.text}>
            Dashboard.Earth provides you with the actions needed to thrive in
            the face of climate change and the steps required to achieve them.
            The platform offers meaningful actions you can take to turn your
            citys most audacious environmental goals into reality. One act is
            all it takes to start to turn the tides of climate change.
          </Box>
          <Box className={classes.circle} />
          <img src={dashimg} alt="" className={classes.mobilefront} />

          <Box className={classes.fields}>
            <Box>
              <img src={react} alt="" className={classes.flutterimg} />
              <Box className={classes.androidtext}>React Native</Box>
            </Box>
            <Box>
              <img src={flutter} alt="" className={classes.flutterimg} />
              <Box className={classes.fluttertext}>Flutter</Box>
            </Box>
          </Box>
        </Box>
      </Carousel>
    </div>
  );
}
